  </header>
  <div class="contain">
    <img class="scrolltop" src="../assets/btn-goingup@2x.png" @click="scrolltop(1200)">
    <div class="section">
      <p>自媒體時代，多平台經營又追蹤，不同的APPs: FB, IG開開關關關關開開，分身乏術嗎? SPARKGO從網紅與粉絲的互動需求出發，一次囊括最受歡迎的三個平台。不論你是網紅還是粉絲，都可以輕鬆的掌握各種平 台上的資訊與互動狀態。 SPARKGO提供用戶創立登台計畫，讓內容創作者可以直接從追蹤者身上有機會獲得實質的資助持續創作。</p>
      <p class="info">keywords | 爆紅 go viral, Viral Stars</p>
    </div>
    <div>
      <img class="full" src="../assets/001/case-3-img-1@2x.png">
    </div>
    <div class="section">
      <p class="para_mark">專案挑戰</p>
      <p>
        1. 怎麼建立app內的活動熱絡程度，讓平常習慣在各大平台切換的使用者<br>
        2. 技術開發時間不足的狀態之下，如何有效地規劃不同開發階段功能<br>
        3. 如何深化互動，讓金融科技可以發揮其作用而不顯得突兀<br>
      </p>
      <p class="para_mark">設計規劃總覽</p>
      <p>使用TAB Bar做為全域導航架構，盡可能地使主要目的與流程操作起來扁平化，容易學習且架構上也容易識別。</p>
    </div>
    <img src="../assets/001/case-3-img-2.png">
    <div class="section">
      <p class="para_mark">開發階段(Phases)劃分</p>
      <p>
        Phase 1  統整動態：<br>
        以台灣網紅聚集的三個平台為主(Facebook, Instagram, Youtube)，將用戶所追蹤的動態依照時間序呈現在主頁面，不同於Flipboard，只要是社群網站用戶便可以SPARKGO，Flipboard是針對『媒體單位』的內容統整，而非單一用戶或是單一專頁。這個階段是將勾勒出一個軟體的基本架構。
        <br><br>
        Phase 2  聚焦互動，讓社群活絡：<br>
        在整理出統整呈現之後，SPARKGO面臨的挑戰是，怎麼讓用戶轉移過來，以及要怎麼在 SPARKGO上面創作互動。並且不能讓整體體驗過於複雜，造成用戶認知與學習上的負擔。
        在留言中點播下一次的創作內容是常見的互動模式，因此用戶可以藉由參與網紅的任務，對追蹤的網紅點播想要看到的東西。
        發起任務提供一個讓追蹤以及被追蹤者不需要跳離系統，藉由任務的贊助參與，自動群組成一個VIP的互動區域。
        <br><br>
        Phase3深化互動，社群經營輔助：<br>
        除了留言點播之外，針對網紅使用者，管理粉絲的點播以及刺激並且深化跟粉絲們的互動是一件很重要而且繁瑣的事情。因此在第三階段的開發，聚焦於網紅與粉絲之間的深化互動。
      </p>
    </div>
      <img src="../assets/001/case-3-img-3@2x.png">
