import Vue from 'vue'
import Router from 'vue-router'
import one from '@/components/one'
import two from '@/components/two'
import ikea from '@/ikea.vue'
import template from '@/components/template'
import inside from '@/components/inside'
import inside001 from '@/components/inside001'
import inside002 from '@/components/inside002'
import inside003 from '@/components/inside003'
import inside004 from '@/components/inside004'
import inside005 from '@/components/inside005'
import cv from '@/components/cv'

Vue.use(Router)

export default new Router({
  	routes: [
    {
      path: '/',
      name: '',
      component: ikea,
      children:[
        {
          path: '',
          name: 'one',
          component: one
        },
        {
          path: 'two',
          name: 'two',
          component: two
        },
      ]
    },
    {
      path:'/inside',
      name: 'inside',
      component: inside,
      children:[
        {
          path: 'template',
          name: 'template',
          component: template
        },
        {
          path: '001',
          name: 'inside001',
          component: inside001
        },
        {
          path: '002',
          name: 'inside002',
          component: inside002
        },
        {
          path: '003',
          name: 'inside003',
          component: inside003
        },
        {
          path: '004',
          name: 'inside004',
          component: inside004
        },
        {
          path: '005',
          name: 'inside005',
          component: inside005
        }
      ]
    },
    {
      path:'/cv',
      name: 'cv',
      component: cv,
    }
  ]
})



