<template>
  <div class="one">
    <router-link class="header-list" :to ="{path: '/two'}"><img src="../assets/header-list@2x.png"></router-link>
    <div class="projects mobile">
        <div class="project" v-for="project in projects">
          <router-link :to="project.link">
            <img class="card-m-works" :src="project.img">
            <p>{{project.tittle}}<br>{{project.tittle2}}</p>
          </router-link>
        </div>
    </div>
    <div class="projects desktop">
        <div class="project" v-for="project in projects">
          <router-link :to="project.link">
            <img class="card-m-works" :src="project.dimg">
            <p>{{project.tittle}}<br>{{project.tittle2}}</p>
          </router-link>
        </div>
    </div>
  </div>
</template>

<script>
import card001 from '../assets/card-m-works-001@2x.png'
import card002 from '../assets/card-m-works-002@2x.png'
import card003 from '../assets/card-m-works-003@2x.png'
import card004 from '../assets/card-m-works-004@2x.png'

import dcard001 from '../assets/cover-001@2x.png'
import dcard002 from '../assets/cover-002@2x.png'
import dcard003 from '../assets/cover-003@2x.png'
import dcard004 from '../assets/cover-004@2x.png'


export default {
  name: 'one',
  data () {
    return {
      projects:[
      	{
      		img : card001,
          dimg : dcard001,
      		tittle : 'SPARKGO APP Design',
      		tittle2 : '',
          link: 'inside/001'
      	},
      	{
      		img : card002,
          dimg : dcard002,
      		tittle : '行南旅 刊物設計',
      		tittle2 : '',
          link: 'inside/002'
      	},
      	{
      		img : card003,
          dimg : dcard003,
      		tittle : 'OXYGENER App',
      		tittle2 : 'Health consult service',
          link: 'inside/003'
      	},
      	{
      		img : card004,
          dimg : dcard004,
      		tittle : 'Superlanding',
      		tittle2 : 'Official Website Design',
          link: 'inside/004'
      	},
        {
          img : card004,
          dimg : dcard004,
          tittle : 'Superlanding',
          tittle2 : 'Official Website Design',
          link: 'inside/005'
        }
      ]
    }
  },
  mounted:function(){
 	$('.projects').slick({
 		infinite: false,
		speed: 300,
		slidesToShow: 1,
		centerMode: true,
		variableWidth: true,
		arrows: false,
 	});
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.header-list{
  height: 54px;
  width: 154px;
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  margin: auto;
  padding-right: 100px;
  transition: .3s;
  img{
    width:100%;
  }
  @media(max-width: 350px){
    top: 36px;
    }
}
.listClose{
  width: 54px;
  padding-right: 0;
  top: 60px;
}

.projects{
  margin-top: 36px;
  @media(min-width: 768px){
    display:flex;
    align-items:center;
    justify-content:center;
    height: 66vh;
  }
}


.mobile{
  @media(min-width: 768px){
    display: none;
  }
  .card-m-works{
    margin:auto;
    border-radius: 10px;
    width: 180px;
    box-shadow: 0 1px 4px 0 rgba(68, 35, 6, 0.38);
    @media(max-width: 350px){
      width: 130px;
    }
  }
  .project{
    width: 180px;
    margin: 0 35px;
    @media(max-width: 350px){
      width: 130px;
      margin: 0 40px;

    }
    a{
      text-decoration: none;
    }
  }
  p{
    font-family: Futura;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 0.6px;
    text-align: center;
    color: #000000;
    margin-top: 20px;
    margin-bottom: 0;
    @media(max-width: 350px){
      margin-top: 10px;
    }
  }
}


.desktop{
  @media(max-width: 768px){
    display: none;
  }
  .card-m-works{
    margin:auto;
    border-radius: 10px;
    width: 450px;
    box-shadow: 0 1px 4px 0 rgba(68, 35, 6, 0.38);
    @media(max-width: 350px){
      width: 130px;
    }
  }
  .project{
    width: 450px;
    margin: 0 125px;
    a{
      text-decoration: none;
    }
  }
  p{
    font-family: Futura;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 0.8px;
    text-align: center;
    color: #000000;
    margin-top: 16px;
    margin-bottom: 0;
  }
}




</style>



